export default {
	data() {
		return {
			globalData: {},
			list: {}
		};
	},
	onLoad() {
		this.globalData = getApp().globalData;
		this.loadData();
	},
	methods: {
		loadData() {
			this.$minApi
				.goods({ category_id: 6 })
				.then(res => {
					this.list = res;
				})
				.catch(err => {
					console.log(err);
				});
		},
		toGoodsDetails(id) {
			uni.navigateTo({
				url: '/pages/goods/details?id=' + id + '&keyword=' + this.globalData.userInfo.keyword
			});
		},
		toPay(id) {
			uni.navigateTo({
				url: '/pages/order/ready?goods=' + id
			});
		}
	}
};
